	import uniCard from '@/components/uni-card/uni-card.vue'
    import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar.vue"
    export default {  
        data() {  
            return {  
             titleBg:'rgba(255,255,255,0)',
			 // 当前选中tab索引
			 // 当前选中tab索引
			 				current_tab: 0,
			 				// 当前滚动条距离左边的位置
			 				scroll_left: 0,
			 				// 当前滚动条滚动到id位置
			 				scroll_into: '',
			 				// 屏幕宽度
			 				screen_width: 0,
			 				tab_bar: [{
			 						id: 'emotion',
			 						tit: '情感'
			 					},
			 					{
			 						id: 'news',
			 						tit: '新闻'
			 					},
			 					{
			 						id: 'diagrams',
			 						tit: '八卦'
			 					},
			 					{
			 						id: 'house',
			 						tit: '楼市'
			 					},
			 					{
			 						id: 'cars',
			 						tit: '骑车'
			 					},
			 					{
			 						id: 'healthy',
			 						tit: '健康'
			 					},
			 					{
			 						id: 'workplace',
			 						tit: '职场'
			 					},
			 					{
			 						id: 'all',
			 						tit: '全部'
			 					},
			 					{
			 						id: 'other',
			 						tit: '其他'
			 					},
			 					{
			 						id: 'music',
			 						tit: '音乐'
			 					},
			 					{
